import React, { useEffect, useState } from 'react';
import './Titlebar.css';

import hamburger from '../../images/misc/hamburger.svg'
import hamburger_white from '../../images/misc/hamburger-white.svg'
import github_grey from '../../images/misc/github-grey.svg'
import email_grey from '../../images/misc/email-grey.svg'
import linkedin_grey from '../../images/misc/linkedin-grey.svg'
import github_white from '../../images/misc/github-white.svg'
import email_white from '../../images/misc/email-white.svg'
import linkedin_white from '../../images/misc/linkedin-white.svg'

import Dropdown from 'react-bootstrap/Dropdown'

class Titlebar extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			showMenu: false,
		}
	}

	showMenu = () => {
		this.setState({
			showMenu: !this.state.showMenu
		})
	}

	handleResize = () => {
		if (window.innerWidth > 700) {
			this.setState({
				showMenu: false,
			})
		}
	}

	render() {
		window.addEventListener("resize", this.handleResize);

		const bgColor = this.props.bg ? {backgroundColor: this.props.bg, color: 'white'} : {}
		const hamburgerColor = this.props.bg ? {backgroundColor: 'black', color: 'white'} : {}
		const titlebarCenter = this.props.bg ? {visibility: 'hidden'} : {}
		const icons = this.props.bg ? [github_white, email_white, linkedin_white] : [github_grey, email_grey, linkedin_grey]
	  return (
	  	<div className="titlebar-container">
	  	<div style={this.state.showMenu ? {display: 'flex', ...hamburgerColor} : {display: 'none'}} className="hamburger-menu" >
	  		<div className="hamburger-menu__text"><a href='/' className='link'>Work</a></div>
	  		<div className="contact-buttons">
		    	<div className="hamburger-menu__text nav-button"><a href = "//github.com/uuberoy" style={{height:"30px"}}><img src={github_white} height={"30px"}/></a></div>
		    	<div className="hamburger-menu__text nav-button"><a href = "mailto: uuberoy@princeton.edu" style={{height:"30px"}}><img src={email_white} height={"30px"}/></a></div>
	  			<div className="hamburger-menu__text nav-button"><a href = "//linkedin.com/in/urvashi-uberoy-951b34136/" style={{height:"30px"}}><img src={linkedin_white} height={"30px"}/></a></div>
		  	</div>
	  		<div className="hamburger-menu__text"><a href='/about' className='link'>About</a></div>
	  	</div>
	  	<div className="titlebar" style={this.state.showMenu ? {top: '50px', ...bgColor} : {top: '0px', ...bgColor}}>
	  		<div className="titlebar-left">
	  			<div className="nav-item" style={{ marginLeft: '5vw', ...bgColor}}><a href='/code' className='link'>Code</a></div>
	  			<div className="nav-item" style={{...bgColor}}><a href='/design' className='link'>Design</a></div>
	  			<div className="nav-item" style={{...bgColor}}><a href='/' className='link'>All</a></div>
	  		</div>
		  	<div style={{...titlebarCenter}} className="titlebar-center" >
			    <div className="title"><a href='/about' className='link'>
			    	URVASHI UBEROY
			    </a></div>
			    <div className="byline">
			    	Coder | Designer
			    </div>
		    </div>
		    <div className="titlebar-right">
		    	<div className="nav-item" style={{...bgColor}}><a href='/about' className='link'>About</a></div>
		    	<div className="contact-buttons">
			    	<div className="nav-item nav-button"><a href = "//github.com/uuberoy" style={{height:"30px"}}><img src={icons[0]} height={"30px"}/></a></div>
			    	<div className="nav-item nav-button"><a href = "mailto: uuberoy@princeton.edu" style={{height:"30px"}}><img src={icons[1]} height={"30px"}/></a></div>
		  			<div className="nav-item nav-button"><a href = "//linkedin.com/in/urvashi-uberoy-951b34136/" style={{height:"30px"}}><img src={icons[2]} height={"30px"}/></a></div>
		  		</div>
		    	<div className="hamburger-container">
		    		<img src={this.props.bg ? hamburger_white : hamburger} className="hamburger" onClick={this.showMenu}/>
		    	</div>
		    </div>
	    </div>
	    </div>
	  );
	}
}

export default Titlebar;